@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
body{max-width:1192px;margin:0 auto;padding:0 64px;font-family:sohne,"Helvetica Neue",Helvetica,Arial,sans-serif}
.main{display:flex}.section-left{width:calc((100% / 3) * 2);padding:0 16px}.section-right{width:calc(100% / 3);margin:0 16px;padding-left:56px}
.nav{width:100%;height:65px;display:flex;justify-content:space-between;align-items:center;box-shadow:0px 4px 12px rgba(0,0,0,.05);margin-bottom:64px}.nav-left{display:flex;align-items:center}.nav-right{display:flex;align-items:center}
.medium-logo{height:32px;margin-right:10px}
.divider__vertical{width:0;height:29px;border-right:1px solid #e6e6e6}
.hyperlink__topic{margin:7px 0 0 10px;font-size:20px;font-weight:400;color:#292929;text-decoration:none}.hyperlink__sign-in{margin-left:16px;font-family:medium-content-sans-serif-font,-apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,Oxygen,Ubuntu,Cantarell,"Open Sans","Helvetica Neue",sans-serif;font-size:16px;font-weight:400;color:#1a8917;text-decoration:none}.hyperlink__join{font-size:14px;color:#757575;text-decoration:none}.hyperlink__related-topics{font-size:13px;font-weight:400;line-height:18px;color:#757575;text-decoration:none}
.button__primary{margin-left:16px;border-radius:99em;border:1px solid #1a8917;padding:7px 16px 9px;background-color:#fff;font-size:14px;font-weight:400;line-height:20px;color:#1a8917}.button__secondary{border-radius:99em;border:1px solid #757575;padding:4px 12px 6px;background-color:#1a8917;font-size:14px;font-weight:400;line-height:20px;color:#fff}
.featured-article{margin-bottom:40px}.featured-article__image{width:100%;max-height:382px;object-fit:cover}.featured-article__headings>.h1__article{margin-top:13px}.featured-article__headings>.h3__article--primary{margin-top:10px}.featured-article__details{display:flex;margin-top:16px}.featured-article__details--right{margin-left:16px}.featured-article__details--right--info{display:flex}
.h1__article,.h2__topic--nav,.h2__topic--side-bar,.h3__article--primary,.h3__article--secondary,.p__topic--sidebar,.p__author-publication,.p__date-and-min-read{margin:0}.h1__article{font-size:32px;font-weight:700;line-height:40px;color:#292929}.h2__topic{border-bottom:1px solid #ccc;padding-bottom:8px;font-size:13px;font-weight:700;line-height:18px;color:#292929}.h2__topic--side-bar{font-family:gt-super,Georgia,Cambria,"Times New Roman",Times,serif;font-size:24px;font-weight:400;line-height:28px;color:#080808}.h3__article--primary{font-size:16px;font-weight:400;line-height:20px;color:#757575}.h3__article--secondary{font-size:16px;font-weight:700;line-height:20px;color:#292929}.h4__article--content{font-size:16px;font-weight:400;line-height:20px;color:#757575}.p__topic--sidebar{font-size:14px;font-weight:400;line-height:20px;color:#757575}.p__author-publication{font-size:13px;font-weight:400;line-height:20px;color:#292929}.p__date-and-min-read{font-size:13px;font-weight:400;line-height:20px;color:#757575}
.image__article--avatar{width:40px;height:40px;border-radius:50%;border:1px solid #1a8917;padding:3px}.image__article__latest{width:152px;height:137px;object-fit:cover}.image__article--popular{width:55px;height:55px;object-fit:cover}
.latest-article{display:flex;justify-content:space-between;padding:12px 0}.latest-article__left{padding-right:24px}.latest-article__left--info{display:flex;justify-content:space-between;align-items:center}.latest-article__right{width:152px;height:137px}
.sidebar__topic>.button__secondary{margin-top:20px}.sidebar__topic>.p__topic--sidebar:nth-child(4){margin-top:23px}
.sidebar__related-topics-container>.h2__topic{margin-top:48px}.sidebar__related-topics-list{display:flex;flex-direction:column}.sidebar__related-topics-list>.hyperlink__related-topics:nth-child(1){margin-top:20px}.sidebar__related-topics-list>.hyperlink__related-topics:nth-child(n+2){margin-top:8px}
.sidebar__articles-popular-container>.h2__topic{margin-top:48px}.sidebar__articles-popular-list{display:flex;justify-content:space-between;margin:16px 0}.sidebar__articles-popular-list--summary{margin-right:16px}.sidebar__articles-popular-list--summary>.h3__article--secondary{margin-bottom:4px}
